import { useState } from 'react'
import { Outlet, useLocation, useNavigate } from 'react-router-dom'
import { Layout, Menu, Button, Drawer, Dropdown, ConfigProvider, Switch } from 'antd'
import {
  MenuOutlined,
  GlobalOutlined,
  RocketOutlined,
  FileTextOutlined,
  QuestionCircleOutlined,
  // HistoryOutlined,
  // AppstoreOutlined,
  ThunderboltOutlined,
  BookOutlined,
  SettingOutlined,
  GroupOutlined,
  ApiOutlined,
  TagOutlined,
  CodeOutlined,
  SunOutlined,
  MoonOutlined,
} from '@ant-design/icons'
import { useTranslation } from 'react-i18next'
import type { MenuProps } from 'antd'
import { useSidebar } from '@/contexts/SidebarContext'
import { useTheme } from '@/contexts/ThemeContext'
import { useDocumentTitle } from '@/hooks/useDocumentTitle'
import { SIDEBAR_COLLAPSED_WIDTH, SIDEBAR_WIDTH } from '@/constants'

const { Header, Sider, Content } = Layout

/**
 * 
 * 
 * @returns React.ReactElement
 */
const MainLayout = () => {
  const { t, i18n } = useTranslation()
  const location = useLocation()
  const navigate = useNavigate()
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)
  const { collapsed, setCollapsed } = useSidebar()
  const { isDark, toggleTheme } = useTheme()

  // 
  useDocumentTitle()

  const isHomePage = location.pathname === '/'

  // 
  const sideMenuItems: MenuProps['items'] = [
    {
      key: 'start',
      icon: <RocketOutlined />,
      label: t('nav.start'),
      children: [
        {
          key: '/introduction',
          icon: <FileTextOutlined />,
          label: t('nav.introduction'),
          onClick: () => navigate('/introduction'),
        },
        {
          key: '/quick-start',
          icon: <ThunderboltOutlined />,
          label: t('nav.quickStart'),
          onClick: () => navigate('/quick-start'),
        },
        {
          key: '/faq',
          icon: <QuestionCircleOutlined />,
          label: t('nav.faq'),
          onClick: () => navigate('/faq'),
        },
        // {
        //   key: '/changelog',
        //   icon: <HistoryOutlined />,
        //   label: t('nav.changelog'),
        //   onClick: () => navigate('/changelog'),
        // },
      ],
    },
    {
      key: 'user-manual',
      icon: <BookOutlined />,
      label: t('nav.userManual'),
      children: [
        {
          key: '/transcode-config',
          icon: <SettingOutlined />,
          label: t('nav.transcodeConfig'),
          onClick: () => navigate('/transcode-config'),
        },
        {
          key: '/framework-prompt-config',
          icon: <CodeOutlined />,
          label: t('nav.frameworkPromptConfig'),
          onClick: () => navigate('/framework-prompt-config'),
        },
        {
          key: '/selection-grouping-sampling',
          icon: <GroupOutlined />,
          label: t('nav.selectionGroupingSampling'),
          onClick: () => navigate('/selection-grouping-sampling'),
        },
        {
          key: '/component-mapping',
          icon: <ApiOutlined />,
          label: t('nav.componentMapping'),
          onClick: () => navigate('/component-mapping'),
        },
        {
          key: '/module-marking',
          icon: <TagOutlined />,
          label: t('nav.moduleMarking'),
          onClick: () => navigate('/module-marking'),
        },
      ],
    },
    // {
    //   key: '/architecture',
    //   icon: <AppstoreOutlined />,
    //   label: t('nav.architecture'),
    //   onClick: () => navigate('/architecture'),
    // },
  ]

  // 
  const languageItems: MenuProps['items'] = [
    {
      key: 'zh-CN',
      label: '',
      onClick: () => {
        i18n.changeLanguage('zh-CN')
        localStorage.setItem('language', 'zh-CN')
      },
    },
    {
      key: 'en-US',
      label: 'English',
      onClick: () => {
        i18n.changeLanguage('en-US')
        localStorage.setItem('language', 'en-US')
      },
    },
  ]

  /**
   *  Key
   */
  const getSelectedKeys = () => {
    const path = location.pathname
    if (path === '/') return ['/']
    return [path]
  }

  /**
   *  Key
   */
  const getOpenKeys = () => {
    return ['start', 'user-manual']
  }

  /**
   * 
   *  Ant Design Theme Token 
   */
  const sideMenuTheme = {
    components: {
      Menu: {
        // 
        itemSelectedBg: isDark ? '#177ddc33' : '#e6f4ff',
        // Ant Blue
        itemSelectedColor: isDark ? '#177ddc' : '#1677ff',
        // 
        itemHoverBg: isDark ? 'rgba(255, 255, 255, 0.08)' : 'rgba(0, 0, 0, 0.04)',
        // 
        colorText: isDark ? '#ffffffd9' : '#374151',
      },
    },
  }

  return (
    <Layout className={`min-h-screen ${isDark ? 'dark' : ''}`}>
      {/* Header */}
      <Header
        className={`fixed top-0 left-0 right-0 z-50 flex items-center justify-between h-16 px-4 border-b shadow-sm md:px-6 ${isDark ? 'bg-[#141414] border-gray-700' : 'bg-white border-gray-200'}`}
      >
        <div className="flex items-center gap-4">
          {/* Mobile menu button */}
          <Button type="text" icon={<MenuOutlined />} onClick={() => setMobileMenuOpen(true)} className="md:hidden" />
          {/* Logo */}
          <div className="flex items-center gap-2 cursor-pointer" onClick={() => navigate('/')}>
            <img src="https://cdn-y.tencentmusic.com/musician/commonPic/cos_d8100545cfbfbf642fbfd4aab126367575652061.png" alt="Sloth D2C" className="w-8 h-8" />
            <span className={`hidden text-lg font-semibold sm:inline ${isDark ? 'text-gray-100' : 'text-gray-800'}`}>Sloth D2C</span>
          </div>
        </div>

        <div className="flex items-center gap-2">
          {/* Theme switcher */}
          <Switch checked={isDark} onChange={toggleTheme} checkedChildren={<MoonOutlined />} unCheckedChildren={<SunOutlined />} />
          {/* Language switcher */}
          {/* <Dropdown menu={{ items: languageItems }} placement="bottomRight">
            <Button type="text" icon={<GlobalOutlined />}>
              <span className="hidden sm:inline">{i18n.language === 'zh-CN' ? '' : 'English'}</span>
            </Button>
          </Dropdown> */}
        </div>
      </Header>

      <Layout className="mt-16">
        {/* Desktop Sidebar */}
        {!isHomePage && (
          <Sider
            width={SIDEBAR_WIDTH}
            collapsedWidth={SIDEBAR_COLLAPSED_WIDTH}
            collapsible
            collapsed={collapsed}
            onCollapse={setCollapsed}
            className={`fixed bottom-0 left-0 z-40 hidden overflow-y-auto border-r md:block top-16 ${isDark ? 'bg-[#141414] border-gray-700' : 'bg-white border-gray-100'}`}
            theme={isDark ? 'dark' : 'light'}
          >
            {/*  ConfigProvider  */}
            <ConfigProvider theme={sideMenuTheme}>
              <Menu
                mode="inline"
                selectedKeys={getSelectedKeys()}
                defaultOpenKeys={getOpenKeys()}
                items={sideMenuItems}
                className="px-2 py-4 border-none" //  padding 
              />
            </ConfigProvider>
          </Sider>
        )}

        {/* Mobile Drawer */}
        <Drawer
          title="Sloth D2C"
          placement="left"
          onClose={() => setMobileMenuOpen(false)}
          styles={{
            body: { padding: 0 },
          }}
          open={mobileMenuOpen}
          width={SIDEBAR_WIDTH}
        >
          <ConfigProvider theme={sideMenuTheme}>
            <Menu
              mode="inline"
              selectedKeys={getSelectedKeys()}
              defaultOpenKeys={getOpenKeys()}
              items={sideMenuItems}
              className="px-2 py-2 border-none"
              onClick={() => setMobileMenuOpen(false)}
            />
          </ConfigProvider>
        </Drawer>

        {/* Main Content */}
        {/*  */}
        <Content
          className={`min-h-[calc(100vh-64px)] transition-all duration-300 ${isDark ? 'bg-[#000000]' : 'bg-gray-50'} ${
            !isHomePage ? (collapsed ? 'md:ml-[64px]' : 'md:ml-[240px]') : ''
          }`}
        >
          <Outlet />
        </Content>
      </Layout>
    </Layout>
  )
}

export default MainLayout
