/*
 * 
 */

const isOnline = import.meta.env.PROD

let lastEvent = ''
let lastUrl = ''

/**
 * 
 */
const getUniqueKey = (): string => {
  let key = localStorage.getItem('d2c_website_uid')
  if (!key) {
    key = `${Date.now()}_${Math.random().toString(36).substring(2, 15)}`
    localStorage.setItem('d2c_website_uid', key)
  }
  return key
}

/**
 *  beacon 
 */
const customizebeaconReport = (eventName: string, params: Record<string, unknown>) => {
  const uniqueKey = getUniqueKey()

  fetch('https://otheve.beacon.qq.com/analytics/v2_upload?appkey=0WEB0IK7H05E5K4S', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      appVersion: '',
      sdkId: 'js',
      sdkVersion: '4.5.9-web',
      mainAppKey: '0WEB0IK7H05E5K4S',
      platformId: 3,
      common: {
        bizType: 'd2c_website',
        platCode: '0',
        utm_source: '',
        plat: 'tme',
        abtestName: '',
        userStatus: '1',
        accountType: '1',
        uniqueKey: uniqueKey,
        A2: '59x14SkHQFdNp6HrSnYmiSWGeT9tBBFY',
        A8: '',
        A12: navigator.language || localStorage.getItem('language') || 'zh-CN',
        A17: `${window.screen.width}*${window.screen.height}*${window.devicePixelRatio || 1}`,
        A23: '',
        A50: '2846217',
        A76: '0WEB0IK7H05E5K4S_' + Date.now(),
        A101: navigator.userAgent,
        A102: '',
        A104: '',
        A119: '',
        A153: '',
      },
      events: [
        {
          eventCode: eventName,
          eventTime: '' + Date.now(),
          mapValue: {
            ...params,
            A99: 'Y',
            A100: '370',
            A72: '4.5.9-web',
            A88: '1744294371735',
          },
        },
      ],
    }),
  }).catch((err) => {
    console.error('Beacon report error:', err)
  })
}

/**
 *  beacon 
 */
const trackEvent = (eventName: string, params: Record<string, unknown>) => {
  if (!eventName) {
    return
  }
  try {
    if (eventName === 'page_pv_uv') {
      customizebeaconReport(eventName, {
        lastUrl,
        fullUrl: location.href,
        ...params,
      })
      lastUrl = params?.url as string
      return
    }
    customizebeaconReport(eventName, {
      lastEvent,
      pageUrl: lastUrl,
      eventCode: eventName,
      ...params,
    })
    lastEvent = eventName
  } catch (err) {
    console.error('Track event error:', err)
  }
}

/**
 * 
 */
export const trackPageView = (url: string, extParam?: Record<string, unknown>) => {
  try {
    if (url && isOnline) {
      trackEvent('page_pv_uv', {
        url,
        ...extParam,
      })
    }
  } catch (err) {
    console.error('Track page view error:', err)
  }
}

/**
 * 
 */
export const trackClickEvent = (tag: string, extParam?: Record<string, unknown>) => {
  try {
    if (tag && isOnline) {
      trackEvent('button_click', {
        eventCode: tag,
        ...extParam,
      })
    }
  } catch (err) {
    console.error('Track click event error:', err)
  }
}
