import { useEffect } from 'react'
import { useLocation } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

/**
 *  i18n key 
 */
const routeTitleMap: Record<string, string> = {
  '/': 'nav.home',
  '/introduction': 'nav.introduction',
  '/quick-start': 'nav.quickStart',
  '/faq': 'nav.faq',
  // '/changelog': 'nav.changelog',
  '/transcode-config': 'nav.transcodeConfig',
  '/framework-prompt-config': 'nav.frameworkPromptConfig',
  '/selection-grouping-sampling': 'nav.selectionGroupingSampling',
  '/component-mapping': 'nav.componentMapping',
  '/module-marking': 'nav.moduleMarking',
  '/architecture': 'nav.architecture',
}

const BASE_TITLE = 'Sloth D2C'

/**
 *  document.title
 */
export const useDocumentTitle = () => {
  const location = useLocation()
  const { t } = useTranslation()

  useEffect(() => {
    const path = location.pathname
    const titleKey = routeTitleMap[path]
    
    if (titleKey) {
      const pageTitle = t(titleKey)
      document.title = path === '/' ? BASE_TITLE : `${pageTitle} - ${BASE_TITLE}`
    } else {
      //  markdown 
      const slug = path.replace(/^\//, '')
      document.title = slug ? `${slug} - ${BASE_TITLE}` : BASE_TITLE
    }
  }, [location.pathname, t])
}
